import React, { Component } from 'react'
import Ztext from 'react-ztext'

const Logoview = () => (
    <Ztext
        depth='1rem'
        direction='both'
        event='pointer'
        eventRotation='30deg'
        eventDirection='default'
        fade={true}
        layers={10}
        perspective='500px'
    >
        <span>
            <strong>Soumik Bhattacharya</strong>
        </span>
    </Ztext>
)

class Logo extends Component {
    render() {
        return (
            <div className="card bg-white border-0">
                <div className="card-body">
                    <h3 className="text-center text-primary"><Logoview /></h3>
                </div>
            </div>
        );
    }
}

export default Logo;