import React, { Component } from 'react';

class Newcontent extends Component {
    render() {
        return (
            <div className="card border-0">
                <div className="card-header text-white bg-primary">Digital India Initiative</div>
                <div className="card-body animate__animated animate__jackInTheBox animate__delay-1s">
                    My goal is to provide vocational technical education at minimal cost.
                    So, I am working with NGOs to provide IT knowledge among the rural youth and to
                    achieve a common goal.
                </div>
            </div>
        );
    }
}

export default Newcontent;