import React, { Component } from 'react'

class About extends Component {
    render() {
        return (
            <div className="card border-0">
                <div className="card-body animate__animated animate__jackInTheBox animate__delay-1s">
                    Hello, I am a <span className="badge bg-primary">Full Stack Developer</span> designer and an
                    open source contributor from Kolkata, West Bengal, India. <br/>
                    I am immensely passionate about learning modern technologies and have keen interest to work
                    on the same. <br />
                    As a co-founder, I started Exosis Software (2017-2021) with a simple goal of providing high
                    quality technical education in rural areas and providing IT solutions at affordable cost.
                </div>
            </div>
        );
    }
}

export default About;